

  .page-container{
    padding: 120px 15px 40px 15px;
    margin: 0px;
    background-color: rgb(255, 255, 255);
    
  }
  .no-select{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    
  }
  .logo{
    width: 30%;
    text-align: center;
    color: #172A3A;
  }
  
  .menuButtom{
    display: none;
    height: 50px;
    width: 50px;
    border-radius: 5px;
    margin-top: 20px;
    margin-left: 20px;
    /* box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.2); */
    font-size: 32px;
    color: #172A3A;
    padding-top: 10px;
}
.menuButtom:hover{  
    cursor: pointer;
    color: #09bc8a;
}
.menuShow{
  margin-left: 0px;
  transition: all ease 0.5s;
}
.menuHide{
  margin-left: -350px;
  transition: all ease 0.5s;
}
.smallMenuAreaClose.menuHide{
  margin-left: -1350px !important;
  transition: all ease 0.5s;
}
  .smallMenu{
    display: none;
    position: fixed;
    background-color: #004346;
    color: white;
    padding: 20px;
    height: 100%;
    width: 300px;
    transition: all ease-in-out 200ms;
    box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.5);
    overflow: auto;
    background: rgb(9,188,138);
    background: linear-gradient(0deg, rgba(9,188,138,1) 20%, rgba(23,42,58,1) 100%);
  }
  .smallMenuAreaClose{
    display: none;
    position: fixed;
    background-color: transparent; 
    height: 100%;
    width: 100%;
    transition: all ease-in-out 200ms;    
    overflow: auto;
  }
  .smallMenuClose{
    color: white;
    cursor: pointer;
    float: right;
    font-size: 28px;
  }
  .smallMenuClose:hover{
    color: #09bc8a;
  }
  
  .header{
    width: 100%;
    height: 90px;
    display: inline-flex;
  }
  .navigation{
    text-align: center;
  }
  .navigation ul li{
    list-style: none;
    display: inline-block;
    padding: 15px 20px;
    margin-top: 5px;
    transition: all ease-in-out 200ms;
  }
  .navigation ul li:hover{
    background-color: #75dddd;
    cursor: pointer;
    border-radius: 5px;
  }
  .menuLang:hover{
    background-color: transparent !important;
    cursor: initial !important;
  }
  .BigMenu{
    width: 80%;
  }
  .header{
    position: fixed;
    background-color: white;
    box-shadow: 0px 1px 8px 1px rgb(0 0 0 / 20%);
  }

  @media (max-width: 768px) {
    .logo{
      margin: auto;
    }
    .header{
      text-align: center;
    }
    .menuLang, .BigMenu{
      display: none !important;
    }
    .menuButtom{
      display: initial;
    }
    .smallMenu,.smallMenuAreaClose{
      display: initial;
    }
    .navigation ul li{
      text-align: left;
      padding: 16px;
      width: 90%;
    }
    .navigation ul{
      display: block;
      list-style-type: disc;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      padding-inline-start: 0px;
    }
  
  }
  
  
  